@import '../CssConstantsVariables.scss';


.kaps-secondary-button{
    background-color: $white;
    padding: 8px 24px;
    border-radius: 4px;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    color:$text-color-normal;
    border: none;
    transition: transform 0.2s; 
    border: 1px solid $text-color-normal;

}